import utilIce from './libs/util-ice'; // 
import { pageRouter } from './routerConfig'; // 
// 
// ice  push 
// ice ()
// {
//   name: '',
//     icon: 'folder-o',
//   children: [
//   {
//     name: ' 1',
//     path: '/demo1/',
//   },
//   {
//     name: ' 2',
//     path: '/demo2/',
//   },
// ],
// },

let parentNodes = [];
let asideMenuConfig = [] ;

pageRouter.map(item=>{
  let parentNodesIndex = parentNodes.indexOf(item.parentNode)
  if(parentNodesIndex==-1){
    parentNodes.push(item.parentNode);
    asideMenuConfig[asideMenuConfig.length] = {
      name: item.parentNodeName,
      icon: 'folder-o',
      children:[
        {
          name:item.meta.title,
          path:item.path
        }
      ],
    }
  }else{
    asideMenuConfig[parentNodesIndex].children.push({
      name:item.meta.title,
      path:item.path
    });
  }
})


